import React from 'react';
import PostItem from './PostItem';

const PostList = ({posts, title, remove}) => {

    if(!posts.length) {
        return (
            <h1 style={{
                fontWeight: 'bold', 
                fontSize: '25px', 
                marginTop: '20px', 
                letterSpacing: '5px',
                }}>
                Посты не найдены
            </h1>
        )
    }

    return (
        <>
            <h1 style={{fontSize: '20px', lineHeight: '20px', marginTop: '14px', fontWeight: 'bold',
                letterSpacing: '5px',}}>
                {title}
            </h1>
            {posts.map( (post, index) => (
                <PostItem remove={remove} number={index + 1} post={post} key={post.id}/>
            ))}
        </>
    );
};

export default PostList;