import React  from 'react';
import { NavLink, Route, Routes } from 'react-router-dom';
import Posts from './Posts';
import SinglePagePosts from './SinglePagePosts';

const About = () => {
    return (
        <>
            <header>
                <NavLink to='/posts'>Posts</NavLink>
            </header>
            <Routes>
                <Route path='/posts' element={<Posts />}/>
                <Route path='/posts/:id' element={<SinglePagePosts />}/>
                <Route path='*' element={
                    <pre style={{
                            letterSpacing: '4px', 
                            fontSize: '18px',
                            textAlign: 'center',
                        }}>
                        Page Not Found...
                    </pre>} 
                />
            </Routes>
        </>
    );
};

export default About;